.footer{
  position: absolute;
  width: 100%;
  height: 100px;
}

.footer hr{
  height:1px;
  border-width:0;
  color:#456783;
  background-color:#456783;
  width: 95%;
}

ul{
    position: absolute;
    top: 40%;  width: 100%;  text-align: center;
    transform: translateY(-50%);
    white-space: nowrap;
    padding: 0px;
}

ul li{
    margin-left: 50px;
    margin-right: 50px;
    list-style: none;
    display: inline-block;
    position: relative;
    width: 50px;   height: 40px;
}
ul li a{
    position: relative;
    color: #456783;
    text-align: center;
    padding: 5px;
}

ul li img{
    margin-top: 10px;
    width: 20px;
    height: 20px;
}

ul li span{
    display: inline-block;
    position: absolute;
    background: #456783;
    transition: transform 0.5s;
}
ul li span:nth-of-type(1),ul li span:nth-of-type(2){
    width: 50px;  height: 2px;
}
ul li span:nth-of-type(3),ul li span:nth-of-type(4){
    width: 2px;  height: 40px;
}
ul li span:nth-of-type(1){
    top: 0;  left: 0;
    transform-origin: right;
    transform: scaleX(1);
}
ul li span:nth-of-type(2){
   bottom: 0;   left: 0;
   transform-origin: left;
   transform: scale(1);
}
ul li span:nth-of-type(3){
   top: 0;   left: 0; 
   transform-origin: bottom;
   transform: scale(0);
}
ul li span:nth-of-type(4){
   top: 0;  right: 0;
   transform-origin: top;
   transform: scale(0);
}

/*************  On hover  *****************/
ul li:hover span:nth-of-type(1){
    transform-origin: left;
    transform: scaleX(0);
}
ul li:hover span:nth-of-type(2){
   transform-origin: right;
   transform: scale(0);
}
ul li:hover span:nth-of-type(3){
   transform-origin: top;
   transform: scale(1);
}
ul li:hover span:nth-of-type(4){
   /* top: 0;  right: 0; */
   transform-origin: bottom;
   transform: scale(1);
}

/* responsive */
@media (max-width:640px) {
    .footer{
        margin-top: 100px;
        margin-bottom: 50px;
        width: 1000px;
    }

    .footer hr{
        margin-left: 100px;
        width: 100%;
    }

    .footer ul{
        margin-left: 100px;
    }
    
}


@media (min-width:641px) and (max-width:1007px) {

    .footer hr{
        margin-left: 100px;
        width: 1300px;
    }
    
    .footer ul{
        margin-left: 250px;
    }
}


@media (min-width:1008px) and (max-width:1300px){
    .footer hr{
        margin-left: 150px;
        width: 1300px;
    }

    .footer ul{
        margin-left: 200px;
    }
    
}









