.about{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:auto;
  }
  
.about .aboutleft .greet{
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-left: 40px;
    -webkit-animation-delay: 2.3s;
    -moz-animation-delay: 2.3s;
    animation-delay: 2.5s;
    transition-delay: 2.5s;
  }

.errorname{
  color: black;
}
  
.hello, .iam, .name{
    font-family: 'Monoton', cursive;
    font-size: 120px;
    font-weight: 10;
    color: #456783;
    margin-top: 0;
    margin-bottom: 0;
  }

.aboutright{
  margin-right: 40px;
}
  
.aboutdesc h1{
    font-size: 20px;
    color: #456783;
    font-weight: 800px;
  }

.about .aboutright .aboutdesc{
    margin-left: 20px;
    transition-delay: 2.5s;
    animation-delay: 2.5s;
  }
  
.about .aboutright .backabout{
    z-index: -9999;
    -webkit-animation-delay: 0.5s;
    -moz-animation-delay: 0.5s;
    animation-delay: 0.5s;
    margin-right: 0px;
  }
  
.about .backabout h1{
    font-size: 200px;
    color: #456783;
    margin-top: 50px;
    margin-bottom: 0;
  }
  

  .animate {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  
  
  
  .aboutdescbottom{
    margin-top: 110px;
  }
  
  .about .aboutright .diagonalline{
    margin-top: 0px;
    margin-left: 0px;

    position: absolute;
    border-bottom: 4px solid #485563;
    height:10px;
    width:0;
    transform:rotate(90deg);
    transform-origin:left;
    animation:growabout 1s linear forwards;
    animation-delay: 1.7s;
  }
  
  @keyframes growabout{
    to {width:175px;}
  }
  
  .two {
    -webkit-animation-delay: 2.3s;
    -moz-animation-delay: 2.3s;
    animation-delay: 2.3s;
  }
  
  
  /* glitch effect */

.glitch{
  position: relative;
  animation:glitch1 1.5s 1;
  animation-delay: 4.8s;
}

@keyframes glitch1{  
  40%{
    left: -30px;
  }

  41.5%{
    left:100px;
  }

  42.5%{
    left: 0px;
  }
  
  43.5%{
    left:150px;
  }

  45%{
    left:-50px;
  }

  46.5%{
    left: 0;
  }
}

/* bug style */
.bug{
  visibility: hidden;
  /* margin-top: 0px; */
}

.bug p{
  color: #616161;
  font-size: 15px;
  font-weight: bold;
  line-height: 0px;
  color: #485563;
}

.bug img{
  height: 50px;
  width: 50px;
  line-height: 0px;
  /* color: white; */
}
  

/* responsive */

@media(max-width:640px){
  .about{
    margin-top: 600px;
    flex-direction: column;
  }

  .aboutleft{
    margin-left: 200px;
  }

  .greet{
    width: 100%;
  }

  .hello, .iam, .errorname{
    font-size: 160px;
    width:max-content;
  }

  .backabout{
    display: none;
  }

  .aboutright{
    display: flex;
    flex-direction: row;
    margin-left: 200px;
    margin-top: 500px;
  }

  @keyframes growabout{
    to {width:375px;}
  }

  .about .aboutright .aboutdesc{
    transition-delay: 0.2s;
    animation-delay: 0.2s;
    width: 1000px;
    margin-left: 50px;
  }


  .aboutdesc h1{
    font-size: 40px;
  }

  .aboutright{
    margin-bottom: 100px;
  }


  .bug{
    margin-top: 100px;
    width: 1000px;
  }

  .bug p{
    font-size: 40px;
  }


}


@media (min-width:641px) and (max-width:1023px){

  .aboutright{
    margin-left: 180px;
  }

}

@media (min-width:1023px) {
  .aboutright{
    margin-left: 170px;
  }
  
}






