.projects {
  margin-top: 100px;
}

.projects .section{
    -webkit-animation-delay: 0.5s;
    -moz-animation-delay: 0.5s;
    animation-delay: 0.5s;
    margin-top: 0;
    display: flex;
    flex-direction: row;
  }
  
  .projects .section h2{
    margin-left: 50px;
    font-size: 200px;
    /* color: #333; */
    /* color: #1F1F1F; */
    /* opacity: 0.18; */
    color: #456783;
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .projects .projectcontainer{
    display: flex;
    flex-direction: column;
    width: 1000px;
    position: relative;
    left: 15%;
    margin-top: 200px;
  }

.project1, .project2, .project3, .project4, .booksproject{
    display: flex;
    flex-direction: row;
    height: 400px;
  }

.project1{
    display: flex;
    flex-direction: row;
    height: 400px;
    margin-bottom: 100px;
  }

.project2{
    display: flex;
    flex-direction: row;
    height: 400px;
    margin-bottom: 100px;
  }

.project3{
    display: flex;
    flex-direction: row;
    height: 400px;
    margin-bottom: 250px;
  }

.project4{
    display: flex;
    flex-direction: row;
    height: 400px;
    margin-bottom: 100px;
  }

  .booksProject{
    margin-bottom: 200px;
    display: flex;
    flex-direction: row;
  }
  
.project1 .detail, .project3 .detail, .project4 .detail, .booksProject .detail{
    display: flex;
    flex-direction: column;
  }

.project2 .detail{
    display: flex;
    flex-direction: column;
    margin-left: 150px;
  }

.project4 .detail{
    margin-left: 0px;
  }

  .booksProject .detail{
    margin-left: 150px;
  }

.detail .desc{
  /* background-color: rgba(255, 255, 255, 0.06); */
  background-color: #456783;
  backdrop-filter: blur(20px);
  border-radius: 8px;
  /* box-shadow: 1px 1px 2rem rgba(0, 0, 0, 0.3); */
  box-shadow: 1px 1px 2rem #456783;
  width: 500px;
  height: 300px;
  padding-left: 20px;
}

.detail .desc h1{
  font-family: "poppins", sans-serif;
  color: #BDBDBD;
}
  
.projectcontainer .desc h2{
      font-family: "poppins", sans-serif;
      color: #BDBDBD;
      font-size: 1.2em;
  }
  
.project1 .detail .techs, .project2 .detail .techs, .project3 .detail .techs, .project4 .detail .techs, .booksProject .detail .techs{
    margin-top: 5px;
    width: 520px;
      height: 30px;
      /* background-color: rgba(255, 255, 255, 0.06); */
      background-color: #456783;
      backdrop-filter: blur(20px);
      border-radius: 8px;
      /* box-shadow: 1px 1px 2rem rgba(0, 0, 0, 0.3); */
      box-shadow: 1px 1px 2rem #456783;
      text-align: center;
  }

.techs p{
  font-size: 1em;
  margin-top: 5px;
  word-spacing: 3em;
  font-family: "poppins", sans-serif;
  font-weight: bold;
  color: #BDBDBD;
}

.project2 .photo2 img{
    /* width: 400px;
    height: 400px; */
    width: 500px;
    height: 250px;
    border-radius: 60px;
  }

.photo4{
    display: flex;
    flex-direction: row;
    width: 600px;
  }

  .project1 .photo1 img{
    width: 500px;
    height: 250px;
    margin-left: 100px;
  }

  .project3 .photo3 img{
    width: 500px;
    height: 500px;
    margin-left: 100px;
    border-radius: 20px;
  }

  .booksProject .photo2 img{
    width: 400px;
    height: 200px;
  }

.linegrow_project{
    margin-top: 210px;
    margin-left: 20px;
    z-index: 999;
    border-bottom: 4px solid #456783;
    height:10px;
    width:20px;
  }

.lineanimate_project{
  margin-top: 210px;
  z-index: 999;
  border-bottom: 4px solid #456783;
  height:10px;
  transform-origin:left;
  animation:growproject 1s linear forwards;
  animation-delay: 0.2s;
}


  @keyframes growproject{
    to {width:670px;}
  }

  @keyframes growproject_mid {
    to {width: 100px;}
  }

  @media (max-width:640px){

    .projects{
      margin-top: 300px;
    }

  }

  @media (min-width:641px) and (max-width:1007px){
    .lineanimate_project{
      margin-top: 210px;
      z-index: 999;
      border-bottom: 4px solid #616161;
      height:10px;
      width:0;
      transform-origin:left;
      animation:growproject_mid 1s linear forwards;
      animation-delay: 0.2s;
    }

  }